@import "../../styles/variables.scss";

#loader {
  position: fixed;
  top: 50%;
  left: 50%;
  border: 4px solid $color-white;
  border-top: 4px solid $color-dark-blue;
  border-radius: 50%;
  width: 3rem;
  height: 3rem;
  margin-top: -2.6rem;
  margin-left: -0.5rem;
  animation: rotate 2s linear infinite;
  z-index: 500;

  &.margin-added {
    margin-left: -1.2rem;
    margin-top: -2rem;
  }

  @keyframes rotate {
    0% {
      transform: rotate(0deg);
    }

    100% {
      transform: rotate(360deg);
    }
  }

  @media (max-width: $window-m) {
    margin-left: 1rem;
  }

  @media (max-width: $window-s) {
    margin-left: 0;
  }
}
